.card-container-component {
  margin: auto;
  position: relative;
  width: 1600px;
  z-index: 10; }
  .card-container-component h2 {
    color: #fff;
    text-align: center; }

.card-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap; }

@media screen and (max-width: 1599px) {
  .card-container-component {
    width: 1280px; } }

@media screen and (max-width: 1279px) {
  .card-container-component {
    width: 960px; } }

@media screen and (max-width: 959px) {
  .card-container-component {
    width: 640px; } }

@media screen and (max-width: 639px) {
  .card-container-component {
    width: 320px; } }



