.film-crawl h3, .film-crawl h4, .film-crawl h5, .film-crawl p {
  color: #ffd600;
  font-family: 'News Gothic Std', sans-serif;
  font-weight: 900;
  opacity: .8; }

.film-crawl h3 {
  font-family: franklin-gothic-ext-comp-urw, sans-serif;
  font-size: 130px;
  margin-bottom: 22px; }

.film-crawl h3, .film-crawl h5 {
  margin-top: 20px;
  text-align: center; }

.film-crawl h5 {
  margin-bottom: 20px;
  font-size: 60px; }

.film-crawl p {
  font-size: 55px;
  line-height: 1.4;
  margin-top: 0; }

.film-crawl {
  bottom: 0;
  font-size: 350%;
  font-weight: bold;
  height: 50em;
  left: 50%;
  margin-left: -9em;
  overflow: hidden;
  position: absolute;
  text-align: justify;
  -webkit-transform: perspective(300px) rotateX(25deg);
          transform: perspective(300px) rotateX(25deg);
  -webkit-transform-origin: 50% 100%;
      -ms-transform-origin: 50% 100%;
          transform-origin: 50% 100%;
  width: 18em; }

.film-crawl:after {
  background-image: -webkit-gradient(linear, left top, left bottom, from(black), to(transparent));
  background-image: -webkit-linear-gradient(top, black 0%, transparent 100%);
  background-image: -o-linear-gradient(top, black 0%, transparent 100%);
  background-image: linear-gradient(to bottom, black 0%, transparent 100%);
  bottom: 60%;
  content: ' ';
  left: 0;
  pointer-events: none;
  position: absolute;
  right: 0;
  top: 0; }

.crawl-content {
  -webkit-animation: scroll 150s linear 4s infinite;
          animation: scroll 150s linear 4s infinite;
  position: absolute;
  top: 100%; }

@-webkit-keyframes scroll {
  0% {
    top: 100%; }
  100% {
    top: -170%; } }

@keyframes scroll {
  0% {
    top: 100%; }
  100% {
    top: -170%; } }



